function MoreText(button, text) {
  this.text = text;
  this.button = button;
}

MoreText.prototype.requestForm = function requestForm() {
  this.button.addEventListener('click', () => {
    document.querySelector('.feedback__wrapper').scrollIntoView({block: 'center', inline: 'center', behavior: 'smooth'});
  });
};

MoreText.prototype.openText = function openText() {

  this.button.addEventListener('click', () => {
    for (let i = 0; i <= this.text.length; i++) {
      if (this.text[i].style.display === 'block') {
        this.text[i].style.display = 'none';
        this.button.textContent = 'Подробнее';
      } else {
        if (window.innerWidth <= 767) {
          document.querySelector('.moretext-js--first').style.display = 'none';
        }
        this.text[i].style.display = 'block';
        this.button.textContent = 'Свернуть';
      }
    }
  });

};
export const toScroll = new MoreText(document.querySelector('.button--request-js'));
export const handler = new MoreText(document.querySelector('.button--moretext-js'), document.querySelectorAll('.moretext-js'));
